* {
  font-family: "Montserrat", sans-serif;
}

.container_home {
  height: 100vh;
  min-height: 780px;
  min-width: 580px;
  /* background-image: url("resources/background.jpg");
  background-size: cover; */
  background-color: #e5f3f77d;
  opacity: 1;
  background-image: repeating-radial-gradient(
      circle at 0 0,
      #d6d5e0 0,
      #e2e4df 30px
    ),
    repeating-linear-gradient(#ffffff, #8aa513de);
}

.leaderboard {
  /*   display: inline-block; */
  width: 48%;
  /*   background-color: aquamarine; */
  height: 100%;
}

.leaderboard-right button {
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding-top: 10px;
}

.leaderboard-right h1 {
  text-align: center;
  margin-top: 100px;
  font-size: 50px;
  color: rgb(70, 67, 67);
}

.upper {
  /*   background-color: red; */
  padding-top: 10px;
}

.upper p {
  text-align: center;
  font-size: 30px;
  margin: 0;
}

.middle {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

/* .bottom {
  background-color: blue;
}
 */

.btn-link:hover {
  background-color: #f2faa79f;
  color: black;
}

.site-header__logo {
  display: block;
  width: 170px;
  margin-left: auto;
  margin-right: auto;
}

.btn-link {
  width: 40%;
  min-width: 200px;
  height: 50px;
  display: block;
  padding-top: 14px;
  background-color: #f3f5e4;
  margin: 0 auto 0 auto;
  border-radius: 0.2em;
  box-sizing: border-box;
  text-decoration: none;
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: #3a3d51;
  text-align: center;
  transition: all 0.2s;
  margin-bottom: 5px;
}
@media screen and (max-width: 1160px) {
  .wrapper {
    margin-top: 40px;
  }
  .leaderboard-right {
    width: 100%;
  }
  .leaderboard {
    width: 100%;
    margin-bottom: 50px;
  }

  .leaderboard_section {
    margin-left: 100px;
    height: 470px;
  }

  .container_home {
    min-height: 1050px;
  }

  .leaderboard_wrap {
    background: #f3f5e4;
  }

  .tabs ul {
    margin-top: 0;
  }

  .middle {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column-reverse;
            flex-direction: column-reverse;
    max-height: 900px;
  }
  .signout {
    display: none;
    /*     position: absolute;
    margin: 1.1% 1.1% 0 0;
    top: 0;
    right: 0;
    width: 15%;
    min-width: 100px;
    height: 40px;
    padding-top: 9px;
    background-color: #f3f5e4;
    border-radius: 0.2em;
