// Card Primary
.cardPrimary {
  background-color: $tertiary;
  .fields {
    .description {
      color: $white;
    }
    .polygon1,
    .path1 {
      fill: rgba(214, 226, 110, 0.83);
    }

    .path2,
    .path6 {
      fill: rgba(214, 226, 110, 0.7);
    }

    .path3,
    .path5 {
      fill: rgba(214, 226, 110, 0.5);
    }

    .path4,
    .polygon2 {
      fill: rgba(214, 226, 110, 0.226);
    }
    .button {
      background-color: rgb(20, 143, 209);
    }
  }
}

// CARD SECONDARY
