section {
  position: relative;
  min-height: 100vh;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
}
section .container {
  position: relative;
  width: 800px;
  height: 500px;
  background: #fff;
  box-shadow: 0 15px 50px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}
section .container .user {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
}

section .container .user .imgBx {
  position: relative;
  width: 100%;
  height: 100%;
  /* background: #ff0; */
  transition: 1s;
}
section .container .user .imgBx img {
  /* position: absolute; */
  top: 0;
  left: 0;
  width: 75%;
  height: 100%;
  object-fit: cover;
}

section .container .user .imgBx {
  height: 100%;
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 25%;
}

section .container .user .formBx {
  position: relative;
  width: 50%;
  height: 100%;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 4rem;
  transition: 1s;
}
section .container .signinBx .formBx {
  margin-top: 8%;
}
section .container .signupBX .formBx {
  margin-top: 4%;
}
section .container .user .formBx form h2 {
  font-size: 1.8rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 2px;
  text-align: center;
  width: 100%;
  margin-bottom: 10px;
  color: black;
}
section .container .user .formBx form input {
  position: relative;
  width: 100%;
  padding: 10px;
  background: #f5f5f5;
  color: #333;
  border: none;
  outline: none;
  box-shadow: none;
  margin: 8px 0;
  font-size: 14px;
  letter-spacing: 1px;
  font-weight: 300;
}
section .container .user .formBx form input[type="submit"] {
  max-width: 100px;
  background: $primary;
  color: #fff;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 1px;
  transition: 1s;
}
section .container .user .formBx form .signup {
  position: relative;
  margin-top: 20px;
  font-size: 12px;
  letter-spacing: 1px;
  color: #555;
  text-transform: uppercase;
  font-weight: 300;
}
section .container .user .formBx form .signup a {
  font-weight: 600;
  text-decoration: none;
  color: $primary;
}
section .container .user .formBx form .signup a:hover {
  cursor: pointer;
}
section .container .signupBX {
  pointer-events: none;
}
section .container.active .signupBX {
  pointer-events: initial;
}
section .container .signupBX .formBx {
  left: 100%;
}
section .container.active .signupBX .formBx {
  left: 0;
}
section .container .signupBX .imgBx {
  left: -100%;
}

section .container.active .signupBX .imgBx {
  left: 0;
}

/* asdas */
section .container .signinBx .formBx {
  left: 0%;
}
section .container.active .signinBx .formBx {
  left: 100%;
}
section .container .signinBx .imgBx {
  left: 0;
}

section .container.active .signinBx .imgBx {
  left: -100%;
}

/* INPUTS */
.inputBox span {
  position: absolute;
  /* left: 54.5%; */
  left: 21%;
  font-size: 12px;
  font-weight: bold;
  display: inline-block;
  pointer-events: none;
  letter-spacing: 1px;
  color: rgba(0, 0, 0, 0.5);
  transition: all 0.2s;
  margin-top: 5%;
}
.inputBox input:focus ~ span,
.inputBox input:valid ~ span {
  transform: translateY(-24px);
  color: $tertiary;
}

.user .imgBx svg {
  max-width: 70%;
}
